.modal {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.rsvp-button {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 365px;
    height: 365px;
    background-color: #9F8487;
    border-radius: 50%;
    cursor: pointer;
    transition: width 1s, height 1s, top 1s, left 1s, border-radius 1s;
}

.full-modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0%;
    background-color: #9F8487;
    transition: width 1s, height 1s, top 1s, left 1s, border-radius 1s;
}
  
 /* Customize the label (the container) */
.container {
  display: inline-block;
  position: relative;
  padding-left: 5vmin;
  width: 3vmin;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0px;
  width: 0px;
}

/* Create a custom radio button */
.checkmark {
    animation: fade-input 0.6s;
    position: relative;
    display: inline-block;
    top: 0;
    left: 0;
    height: 2vmin;
    width: 2vmin;
    background-color: transparent;
    border-radius: 50%;
    outline: 0.3vmin solid white;
}

/* On mouse-over, add a grey background color */
.container:hover input ~ .checkmark {
  background-color: rgba(255, 255, 255, 0.301);
}

/* When the radio button is checked, add a blue background */
.container input:checked ~ .checkmark {
  background-color: transparent;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.container input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.container .checkmark:after {
  top: 0.6vmin;
  left: 0.6vmin;
  width: 0.8vmin;
  height: 0.8vmin;
  border-radius: 50%;
  background: white;
} 

.input-text-rsvp {
    animation: fade-input 0.6s;
    border-bottom: 0.3vmin solid white;
    width: calc(20px + 20vmax);
    border-top: 0px;
    border-left: 0px;
    border-right: 0px;
    margin-left: 5vmin;
    font-size: calc(1px + 2vmin);
    font-family: inherit;
    font-weight: inherit;
    color: white;
}

input[type="file"] {
  display: none;
}

.input-button-rsvp {
  animation: fade-input 0.6s;
  display: inline-block;
  cursor: pointer;
  color: #FFFFFF;
  font-size: calc(2px + 2vmin);
  padding-left: 20px;
  padding-top: 20px;
}

@keyframes fade-input {
  0% {
      opacity: 0%;
  }
  20% {
    opacity: 0%;
  }
  100% {
      opacity: 100%;
  }
}
